<template>
    <div class="form-in-system-log">
        <el-form>
            <el-form-item label="时间">
                <el-date-picker
                    v-model="value1"
                    type="datetime"
                    placeholder="选择起始日期时间"
                    align="right"
                    :picker-options="pickerOptions"
                    size="mini">
                </el-date-picker>
                <el-date-picker
                    v-model="value2"
                    type="datetime"
                    placeholder="选择结束日期时间"
                    align="right"
                    :picker-options="pickerOptions"
                    size="mini">
                </el-date-picker>
            </el-form-item>
            <div class="checkbox-group">
                <!--                        这个地方写的太丑了，不能这么写，回头要把checklist-group抽出去,同样由sidebar点击事件传入-->
                <el-checkbox-group v-model="checkListpointcategory">
                    <el-checkbox label="事件日志"></el-checkbox>
                    <el-checkbox label="系统日志"></el-checkbox>
                </el-checkbox-group>
            </div>
            <div>
                <el-button type="danger" size="mini" class="submit-button-event-search">搜索</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "FormInSystemLog",
        data() {
            return {

                checkListpointcategory:['选中且禁用','复选框 A'],


                pickerOptions: {
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '昨天',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '一周前',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                },
                sourceOptions: [{
                    sourceValue: '选项1',
                    sourceLabel: 'OPC'
                }, {
                    sourceValue: '选项2',
                    sourceLabel: 'SIS'
                }, {
                    sourceValue: '选项3',
                    sourceLabel: 'GAS'
                }, {
                    sourceValue: '选项4',
                    sourceLabel: '摄像头'
                }, {
                    sourceValue: '选项5',
                    sourceLabel: '服务器'
                }],
                sourceValue: '',

                statusOptions: [{
                    statusValue: '选项1',
                    statusLabel: '已恢复'
                }, {
                    statusValue: '选项2',
                    statusLabel: '未恢复'
                }],
                statusValue: '',

                value1: '',
                value2: '',
                value3: '',
                radio: '1',
                checkListeventlevel:['选中且禁用','复选框 A'],
            };
        }
    };
</script>

<style>
    .el-form-item__label {
        color:white;
    }
    .label-in-event-search {
        color: white;
    }
    .form-in-system-log {
        padding: 4px;
    }
    .submit-button-event-search {
        width: 209px;
        height: 30px;
        font-family: 幼圆;
        border-radius: 2px;
    }
</style>
